.head-cell {
  background-color: $darkgrey;
  color: #ffffff;
  border-right: 2px #f2f2f2 solid;
  box-sizing: border-box;
  text-align: center;
  word-wrap: break-word;
}

.head-cell__rate {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 30px;
}

.head-cell__price {
  font-weight: 300;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 30px;
}

@media (min-width: $tablet-width) {
  .head-cell {
    padding: 4px 33px 4px 33px;
  }
}

@media (min-width: $desktop-width) {
  .head-cell {
    padding: 9px 66px 9px 66px;
  }

  .head-cell__rate {
    font-size: 18px;
  }

  .head-cell__price {
    font-size: 24px;
  }
}
